import Nav from "../../components/Navbar/Nav";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import { useAuth } from "../../Context/auth";
import admin from "../../Images/Admin/Admin.png";
import {
  FcGoogle,
  FcApproval,
  FcGraduationCap,
  FcSalesPerformance,
  FcBriefcase,
} from "react-icons/fc";

const AdminProfile = () => {
  const [auth] = useAuth();

  return (
    <div>
      <Nav />

      <div className="admin_main">
        <hr />
        <div className="d-flex flex-row  container-fluid p-3 ">
          <div className="col-lg-2 col-sm-12">
            <AdminMenu />
          </div>
          <div className="col-lg-10 col-sm-12 ps-5  ">
            <div className="  w-85 ps-5  d-flex flex-row  justify-content-center  ">
              <div className=" text-center p-3 rounded-5">
                {" "}
                <img
                  className=" rounded-3"
                  src={admin}
                  width={200}
                  alt="admin"
                />
              </div>
              <div>
                <h5 className="p-3 text-uppercase">
                  {" "}
                  name :{" "}
                  <span className="text-muted fw-bold">
                    {" "}
                    {auth?.user?.userName}
                    <FcApproval />
                  </span>
                </h5>
                <h5 className="p-3 text-uppercase">
                  {" "}
                  email :{" "}
                  <span className="text-muted fs-6 fw-bold">
                    {auth?.user?.email}
                  </span>
                </h5>

                <h5 className="p-3 text-uppercase">
                  {" "}
                  phone :{" "}
                  <span className="text-muted fw-bold">
                    {auth?.user?.phone}
                  </span>
                </h5>

                <div className="d-flex ps-3 gap-3 flex-row justify-content-start">
                  <FcGoogle className="h2" />

                  <FcGraduationCap className="h2" />

                  <FcSalesPerformance className="h2" />

                  <FcBriefcase className="h2" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <br />
    </div>
  );
};

export default AdminProfile;
